function HistoryTable({el}) {
  return <>
      <tr>
        <td>{el?.transaction_type ? 'Income' : 'Expence'}</td>
        <td>{el?.transaction_category__category_name}</td>
        <td>{el?.transaction_comment}</td>
        <td>{el?.transaction_type ? el?.transaction_sum : `-${el?.transaction_sum}`}</td>
        <td>{el?.transaction_date.split('-').reverse().join('.')}</td>
      </tr>
    </>
}

export default HistoryTable