import { useEffect, useState } from "react"
import { Container, FormControl, InputGroup } from "react-bootstrap"
import Categories from "../addIncomes/Categories"
import HistoryTable from "./HistoryTable";
import "./history.scss"

function IncomesHistory() {
  const [dateValue, setDateValue] = useState('');
  const [dateRangeStartValue, setDateRangeStartValue] = useState('');
  const [dateRangeEndValue, setDateRangeEndValue] = useState('');
  const [historyBody, setHistoryBody] = useState([]);
  const [countTransaction, setCountTransaction] = useState(10);

  // useEffect(() => {
  //   fetch('https://dashakol88.pythonanywhere.com/api/transaction/filter', {
  //     credentials: "include",
  //     mode: "cors"
  //   }).then(data => data.json).then(data => console.log(data))
  // }, [])

  // useEffect(() => {
  //   const ddd = async() => {
  //     await fetch('https://dashakol88.pythonanywhere.com/api/user/login', {method: "POST", mode:"cors", credentials:"include", body:'{"username":"Dasha","password":"testpassword123"}'});
  //     fetch('https://dashakol88.pythonanywhere.com/api/transaction/filter', {
  //         credentials: "include",
  //         mode: 'cors'
  //        }).then(data => data.json).then(data => console.log(data))
  //   }
  //   ddd();
  // }, [])

  useEffect(() => {
    // const getEl = async () => {
    //   const res = await fetch('https://dashakol88.pythonanywhere.com/api/transaction/filter', {
    //     credentials: "include",
    //     mode: 'cors'
    //    }).then(data => data.json())
    //    console.log(res);
    // }
    // getEl()
    fetch('https://dashakol88.pythonanywhere.com/api/transaction/latest', {
        credentials: "include",
        mode: 'cors'
       }).then(data => data.json()).then(data => setHistoryBody(data.transactions))
  }, [])

  // useEffect(
  //   () => {
  //   Promise.all([
  //     new Promise ((resolve) => {
  //       fetch('https://dashakol88.pythonanywhere.com/api/user/login', {method: "POST", mode:"cors", credentials:"include", body:'{"username":"Dasha","password":"testpassword123"}'})
  //     }),
  //     new Promise((resolve) => {
  //       fetch('https://dashakol88.pythonanywhere.com/api/transaction/filter', {
  //         credentials: "include",
  //         mode: 'cors'
  //        }).then(data => data.json).then(data => resolve(data))
  //     })
  //   ]).then(data => setHistoryBody(data[1]))
  //   console.log(historyBody);
  // }, [])
  
  
  // useEffect(() => {
    //   fetch('https://restcountries.com/v3.1/all').then(data => data.json()).then(data => {
      //     setHistoryBody(data)
      //   })
      // }, [])
      
  function showMoreTransaction() {
    setCountTransaction(countTransaction + 10)
  }
  function showLessTransaction() {
    setCountTransaction(countTransaction - 10)
  }

  function dateReset(e) {
    setDateValue(e.target.value);
    setDateRangeStartValue('');
    setDateRangeEndValue('');
  }

  function handleChangeStart(e) {
    setDateValue('');
    setDateRangeStartValue(e.target.value);
  }
  function handleChangeEnd(e) {
    setDateValue('');
    setDateRangeEndValue(e.target.value);
  }

  return<>
    <Container className="pt-3">
      <div className="history-header">
        <div className="add-income-group">
          <select className="add-select">
            <option>Type</option>
            <option>Income</option>
            <option>Expense</option>
          </select>
          <Categories/>
        </div>
        <div className="select-date">
          <div className="one-date">
            <input type={'date'} value={dateValue} onChange={dateReset}></input>
          </div>
          <div className="date-range-group">
            <p className="m-0">From:</p>
            <input className="date-range" type={'date'} value={dateRangeStartValue} onChange={handleChangeStart}></input>
            <p className="m-0">to:</p>
            <input className="date-range" type={'date'} value={dateRangeEndValue} onChange={handleChangeEnd}></input>
          </div>
        </div>
      </div>
      <div className="history-table-title">
        <h2 className="m-0">History</h2>
        <div className="history-buttons-box">
          <button className="button-show-more-less" onClick={showMoreTransaction}>Show more</button>
          <button className="button-show-more-less" onClick={showLessTransaction} disabled={countTransaction <= 10}>Show less</button>
        </div>
      </div>
      <div className="history-table-wraper">
        <table className="table-history">
          <tbody>
          {historyBody.sort((a, b) => a.id > b.id ? -1 : 1).slice(0, countTransaction).map(el => <HistoryTable el={el}/>)}
          </tbody>
        </table>
      </div>
    </Container>
  </>
}

export default IncomesHistory